import {
	AfterViewInit,
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ComponentFactoryResolver,
	ElementRef,
	Inject,
	Input,
	OnDestroy,
	Renderer2,
	Type,
	ViewChild,
	ViewContainerRef,
	ViewEncapsulation
} from '@angular/core';
import { Theme } from '../../themes/theme';
import { themeToken } from '../../themes/theme.token';
import { DialogComponent } from '../common/dialog.component';
import { FabricNestedDialogComponent } from '../common/fabric.nested-dialog.component';
import { FabricDrawerService } from './fabric-drawer.service';

@Component({
	templateUrl: './fabric-drawer.component.html',
	styleUrls: ['./fabric-drawer.ngx.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class FabricDrawerComponent extends DialogComponent implements AfterViewInit, OnDestroy {

	@ViewChild('container', { read: ViewContainerRef, static: false })
	container: ViewContainerRef;

	@Input()
	closeOnClickOutside: boolean = false;

	dialogNestedComponent: Type<FabricNestedDialogComponent>;

	constructor(private readonly componentFactoryResolver: ComponentFactoryResolver,
				private readonly changeDetectorRef: ChangeDetectorRef,
				private readonly elRef: ElementRef,
				private readonly dialogService: FabricDrawerService,
				renderer: Renderer2,
				@Inject(themeToken) theme: Theme) {
		super(elRef, renderer, theme);
	}

	ngAfterViewInit() {
		this.createNestedComponent(this.dialogNestedComponent);
		this.changeDetectorRef.detectChanges();
		this.addTheme();
	}

	ngOnDestroy() {
		super.ngOnDestroy();
	}

	closeDrawer() {
		this.dialogService.close();
	}

	clickOutside(event: any): void {
		if (this.closeOnClickOutside) {
			if (this.isContainerClicked(event)) {
				this.closeDrawer();
			}
		}
	}

	private isContainerClicked(event: any): boolean {
		const dialogContentRef = this.elRef.nativeElement.querySelector('.gui-drawer-content');

		if (dialogContentRef) {
			return !dialogContentRef.contains(event.target);
		}
	}

	private createNestedComponent(component: Type<FabricNestedDialogComponent>): void {
		const componentFactory = this.componentFactoryResolver.resolveComponentFactory(component);

		this.container.createComponent(componentFactory);
	}

}
